;(function(w) {
  document.addEventListener(
    'plusready',
    function() {

      let shares = {}
      plus.share.getServices(
        function(s) {
          for(var i in s) {
            var t = s[i]
            shares[t.id] = t
          }
          console.log(JSON.stringify(shares))
        },
        function(e) {
          console.log('' + e.message)
        }
      )

      var shareAction = function(sb, msg, cb=null) {
        if(!sb || !sb.s) {
          plus.nativeUI.alert('')
          return
        }
        msg.extra = {
          scene: sb.x
        }
        // 
        if(sb.s.authenticated) {
          sb.s.send(
            msg,
            () => {
              cb && cb()
              // plus.nativeUI.alert('' + sb.s.description + '')
            },
            e => {
              plus.nativeUI.alert('' + sb.s.description + ': ' + e.message)
            }
          )
        } else {
          sb.s.authorize(
            () => {
              sb.s.send(
                msg,
                () => {
                  cb && cb()
                  // plus.nativeUI.alert('' + sb.s.description + '')
                },
                e => {
                  plus.nativeUI.alert('' + sb.s.description + ': ' + e.code + e.message)
                }
              )
            },
            e => {
              plus.nativeUI.alert('')
            }
          )
        }
        // if(msg.extra.scene == "WXSceneSession" || msg.extra.scene == "WXSceneTimeline" ){
        //   msg.type = "web";
        // }
      }

      w.wxShare = function(msg, cb=null) {

        let shareBts = []
        console.log(shares)
        // 
        let ss = shares['weixin']
          // console.log(ss&&ss.nativeClient)
        ss &&
        ss.nativeClient &&
        (shareBts.push({
          title: '',
          s: ss,
          x: 'WXSceneTimeline'
        }),
          shareBts.push({
            title: '',
            s: ss,
            x: 'WXSceneSession'
          }))
        shareBts.push({
          title: "QQ",
          s: shares['qq']
        })
        // 
        shareBts.length > 0 ?
          plus.nativeUI.actionSheet({
              title: '',
              cancel: '',
              buttons: shareBts
            },
            e => {
              e.index > 0 && shareAction(shareBts[e.index - 1], msg, cb)
            }
          ) :
          plus.nativeUI.alert('!')
      }
    },
    false
  )
})(window);
