import axios from 'axios'
import config from '@/updateConfig'
var wgtWaiting = null

function downWgt(wgtUrl) {
  wgtWaiting = plus.nativeUI.showWaiting('')
  var wgtOption = {
    filename: '_doc/update/',
    retry: 1
  }
  var task = plus.downloader.createDownload(wgtUrl, wgtOption, function(
    download,
    status
  ) {
    if (status == 200) {
      wgtWaiting.setTitle('')
      installWgt(download.filename)
    } else {
      plus.nativeUI.alert('')
      wgtWaiting.close()
    }
  })

  task.addEventListener('statechanged', function(download, status) {
    switch (download.state) {
      case 2:
        wgtWaiting.setTitle('')
        break
      case 3:
        var percent = (download.downloadedSize / download.totalSize) * 100
        wgtWaiting.setTitle('')
        break
      case 4:
        wgtWaiting.setTitle('')
        break
    }
  })
  task.start()
}

function installWgt(wgtpath) {
  plus.runtime.install(
    wgtpath,
    {},
    function(wgtinfo) {
      wgtWaiting.close()
      plus.nativeUI.alert('', function() {
        plus.runtime.restart()
      })
    },
    function(error) {
      wgtWaiting.close()
      plus.nativeUI.alert('\n' + error.message)
    }
  )
}

